.header {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;

  .header-container {
    display: flex;
    justify-content: space-between;
    padding-top: 18px;
    padding-bottom: 18px;

    @include small-tablet {
      padding-top: 27px;
    }

    img {
      @include for-desktop {
        display: block;
        margin: 0 auto;
      }

      @include small-tablet {
        width: 119px;
        height: 115px;
      }
    }
  }

  .menu-right {
    padding-top: 22px;

    @include tablet {
      display: none;
    }
  }

  .nav-right {
    display: flex;
    padding: 24px 0;
    border-top: 2px solid var(--accent-color);

    &__item {
      font-weight: 500;
      font-size: 18px;
      line-height: 25px;
      text-transform: uppercase;
      color: var(--light-color);
      cursor: pointer;
      transition: 0.3s ease-in-out;

      &:hover {
        color: var(--accent-hover-color);
      }
    }

    &__item:not(:last-child) {
      margin-right: 57px;
    }
  }

  .menu-left {
    padding-top: 8px;

    @include tablet {
      display: none;
    }

    .contacts {
      @include flex-v-center;
      justify-content: space-between;
      padding-bottom: 30px;
    }

    .nav-left {
      display: flex;
      justify-content: flex-end;
      padding: 24px 0;
      padding-left: 20px;
      border-top: 2px solid var(--accent-color);

      @include for-desktop {
        padding-left: 0;
      }

      &__item {
        font-weight: 500;
        font-size: 18px;
        line-height: 25px;
        text-transform: uppercase;
        color: var(--light-color);
        cursor: pointer;
        transition: 0.3s ease-in-out;

        &:hover {
          color: var(--accent-hover-color);
        }
      }

      &__item:not(:last-child) {
        margin-right: 57px;
      }
    }
  }
}
