.hero {
  &__container {
    background-image: linear-gradient(
        180deg,
        #0a0a0a 0%,
        rgba(10, 10, 10, 0) 100%
      ),
      url(../img/bg.jpg);
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    text-align: center;
  }

  &__contant {
    padding-top: 303px;

    @include small-tablet {
      padding-top: 220px;
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 12px;
    font-weight: 500;
    font-size: 32px;
    line-height: 44px;
    text-align: center;
    color: var(--accent-color);

    @include for-desktop {
      font-size: 26px;
    }

    @include small-tablet {
      font-size: 20px;
      line-height: 27px;
    }
  }

  &__subtitle {
    margin: 0;
    margin-bottom: 12px;
    font-weight: 800;
    font-size: 80px;
    line-height: 109px;
    text-align: center;
    text-transform: uppercase;
    color: var(--light-color);

    @include for-desktop {
      font-size: 64px;
    }

    @include small-tablet {
      font-size: 34px;
      line-height: 46px;
    }
  }

  &__title-text {
    display: block;
    margin-bottom: 78px;
    font-weight: 500;
    font-size: 24px;
    line-height: 33px;
    text-align: center;
    color: var(--accent-color);
  }

  &__btn {
    padding-bottom: 200px;

    @include for-desktop {
      padding-bottom: 100px;
    }
  }

  &__btn-link:not(:last-child) {
    margin-right: 40px;

    @include for-desktop {
      margin-right: 12px;
    }
  }

  &__btn-link {
    transition: 0.3s ease-in-out;

    &:hover {
      background: var(--accent-hover-color);
    }
  }
}
