.mail {
  @include flex-v-center;
  padding-left: 20px;

  @include for-desktop {
    padding-left: 0;
  }

  &__link {
    font-weight: 400;
    font-size: 14px;
    line-height: 19px;
    color: var(--light-color);
    margin-left: 11px;
    margin-right: 73px;
    transition: 0.3s ease-in-out;

    &:hover {
      color: var(--accent-hover-color);
    }
  }
}
