.footer {
  &__container {
    background: linear-gradient(180deg, #0a0a0a 0%, rgba(10, 10, 10, 0) 400%),
      radial-gradient(
        61.94% 61.94% at 50% 50%,
        rgba(10, 10, 10, 0) 45.62%,
        #0b0b0b 100%
      ),
      url(../img/bg-footer.jpg);
    @include bg-cover;
  }

  &__logo {
    padding-top: 75px;
    padding-bottom: 60px;

    @include mobile {
      padding: 28px 0;
    }

    img {
      display: block;
      margin: 0 auto;

      @include mobile {
        width: 120px;
        height: 120px;
      }
    }
  }

  .nav {
    padding: 20px 0;
    border-top: 2px solid var(--accent-color);
    border-bottom: 2px solid var(--accent-color);
    margin-bottom: 59px;

    @include small-tablet {
      padding-bottom: 0;
    }

    @include mobile {
      margin-bottom: 32px;
    }

    &__list {
      @include flex-all-sb;

      @include small-tablet {
        flex-wrap: wrap;
      }
    }

    &__item {
      font-weight: 500;
      font-size: 18px;
      line-height: 25px;
      text-transform: uppercase;
      color: var(--light-color);
      transition: 0.3s ease-in-out;
      cursor: pointer;

      @include small-tablet {
        font-size: 16px;
        line-height: 22px;
        padding-bottom: 20px;
      }

      &:not(:last-child) {
        @include small-tablet {
          padding-right: 20px;
        }
      }

      &:nth-child(3) {
        @include mobile {
          padding-right: 0;
        }
      }

      &:hover {
        color: var(--accent-color);
      }
    }
  }

  &__contacts {
    @include flex-v-center;
    flex-wrap: wrap;
    padding-bottom: 147px;

    @include for-desktop {
      padding-bottom: 118px;
    }

    @include small-tablet {
      display: block;
    }

    .contacts__address {
      @include flex-v-center;
      padding-right: 82px;

      @include small-tablet {
        padding: 0;
        padding-bottom: 32px;
      }

      .text {
        margin: 0;
        margin-left: 15px;
        font-weight: 400;
        font-size: 14px;
        line-height: 20px;
        color: var(--accent-color);
        width: 227px;
      }
    }

    .contacts__schedule {
      @include flex-v-center;
      padding-right: 125px;

      @include small-tablet {
        padding: 0;
        padding-bottom: 32px;
      }

      .text {
        margin: 0;
        margin-left: 15px;
        font-weight: 400;
        font-size: 16px;
        line-height: 22px;
        color: var(--accent-color);
      }
    }

    .contacts__phone {
      padding-right: 115px;

      @include for-desktop {
        padding-right: 15px;
      }

      @include small-tablet {
        padding: 0;
        padding-bottom: 32px;
      }

      .phone__link {
        color: var(--accent-color);
        transition: 0.3s ease-in-out;

        &:hover {
          color: var(--light-color);
        }

        img {
          margin-right: 15px;
        }
      }
    }

    .contacts__mail {
      display: flex;
      align-items: center;

      .mail__link {
        padding: 10px 0;
        margin: 0;
        margin-left: 15px;
        color: var(--accent-color);
        transition: 0.3s ease-in-out;

        &:hover {
          color: var(--light-color);
        }
      }
    }
  }
}
