.burger {
  display: none;
  position: fixed;
  right: 0;
  top: 0;
  width: 60px;
  height: 60px;
  background: var(--accent-color);
  border-radius: 0px 0px 0px 20px;

  @include tablet {
    display: block;
  }

  &__line {
    position: absolute;
    left: 50%;
    top: 50%;
    border-radius: 100px;
    height: 2px;
    width: 21px;
    transform: translateX(-50%) translateY(-50%);
    background-color: var(--light-color);

    &:first-child {
      margin-top: -7px;
    }

    &:last-child {
      margin-top: 7px;
    }
  }
}

.burger-menu {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 500;
  overflow-y: auto;
  width: 100%;
  height: 100vh;
  background-color: var(--accent-color);
  padding: 50px;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;

  &__phone {
    // display: flex;
    // align-items: center;
    padding-bottom: 26px;
    margin-bottom: 24px;
    border-bottom: 1px solid rgba(255, 255, 255, 0.2);
  }

  &__phone-link {
    display: block;
    font-weight: 400;
    font-size: 16px;
    line-height: 22px;
    color: var(--light-color);

    @include small-tablet {
      display: block;
    }

    &:first-child {
      margin-right: 50px;

      @include small-tablet {
        margin-right: 0;
        padding-bottom: 16px;
      }
    }
  }

  .nav {
    padding-bottom: 230px;

    &__item {
      font-weight: 500;
      font-size: 18px;
      line-height: 25px;
      text-transform: uppercase;
      color: var(--light-color);
      padding-bottom: 20px;
    }
  }

  &__footer {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    padding-top: 24px;
    border-top: 1px solid rgba(255, 255, 255, 0.2);
  }

  &__footer-mail {
    display: flex;
    align-items: center;

    &:first-child {
      padding-bottom: 29px;
    }
  }

  &__footer-text {
    display: flex;
    align-items: center;

    .text {
      font-weight: 400;
      font-size: 16px;
      line-height: 23px;
      color: var(--light-color);
      margin: 0 10px;
    }
  }
}

.burger-close {
  position: absolute;
  right: 0;
  top: 0;
  width: 60px;
  height: 60px;
  border-radius: 0px 0px 0px 20px;
  background-image: url(../img/close.svg);
  background-position: center;
  background-repeat: no-repeat;
}

.menu--active {
  opacity: 1;
  visibility: visible;
}
