.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  border-radius: 100px;
  padding: 20px 32px;
  font-weight: 500;
  font-size: 16px;
  line-height: 22px;
  text-align: center;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: var(--light-color);
  background-color: var(--accent-color);
  cursor: pointer;

  @include for-desktop {
    font-size: 14px;
    line-height: 19px;
  }
}
