.products {
  &__container {
    background-image: url(../img/products-bg.jpg);
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;

    @include small-tablet {
      background-position: inherit;
    }
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(auto-fill, 255px);
    grid-row-gap: 30px;
    grid-column-gap: 30px;
    padding-bottom: 105px;

    @include small-tablet {
      display: flex;
      gap: 0;
      padding-bottom: 58px;
    }
  }

  .section-title {
    text-align: center;

    &__subtitle {
      margin: 0;
      font-weight: 500;
      font-size: 30px;
      line-height: 140%;
      text-align: center;
      color: var(--accent-color);
      padding-top: 63px;
      padding-bottom: 9px;

      @include small-tablet {
        font-size: 20px;
        line-height: 140%;
        padding-top: 32px;
      }
    }

    &__title {
      position: relative;
      padding-bottom: 47px;

      @include small-tablet {
        padding-bottom: 0;
      }
    }

    &__title::before {
      content: "";
      position: absolute;
      top: 30px;
      left: 260px;
      width: 50px;
      height: 2px;
      background: var(--accent-color);

      @include small-tablet {
        top: 50px;
      }

      @include mobile {
        left: 135px;
      }
    }

    &__title::after {
      content: "";
      position: absolute;
      top: 30px;
      right: 260px;
      width: 50px;
      height: 2px;
      background: var(--accent-color);

      @include small-tablet {
        top: 50px;
      }

      @include mobile {
        right: 135px;
      }
    }
  }

  &__item {
    @include bg-cover;
    max-width: 100%;
    height: 430px;

    @include small-tablet {
      background-size: auto;
    }
  }

  &__item:nth-child(1) {
    background-image: url(../img/botl-1.png);
    transition: background-image 0.2s ease-in-out;
    &:hover {
      background-image: url(../img/botl-1-hover.jpg);
    }
  }
  &__item:nth-child(2) {
    background-image: url(../img/botl-2.png);
    transition: background-image 0.2s ease-in-out;
    &:hover {
      background-image: url(../img/botl-2-hover.jpg);
    }
  }
  &__item:nth-child(3) {
    background-image: url(../img/botl-3.png);
    transition: background-image 0.2s ease-in-out;
    &:hover {
      background-image: url(../img/botl-3-hover.jpg);
    }
  }
  &__item:nth-child(4) {
    background-image: url(../img/botl-4.png);
    transition: background-image 0.2s ease-in-out;
    &:hover {
      background-image: url(../img/botl-4-hover.jpg);
    }
  }

  .swiper-pagination {
    visibility: hidden;
    position: relative;
    bottom: 25px;
    display: flex;
    justify-content: center;
    align-items: center;

    @include small-tablet {
      visibility: visible;
    }

    .swiper-pagination-bullet {
      width: 6px;
      height: 6px;
    }

    .swiper-pagination-bullet-active {
      width: 8px;
      height: 8px;
      background: var(--accent-color);
    }
  }
}
