.graph-modal {
  @include small-tablet {
    display: none;
  }
}

.popup {
  width: 660px;
  border-radius: 0;
  padding: 0;

  .graph-modal__content {
    text-align: center;
  }

  &__top {
    background-image: linear-gradient(
        180deg,
        #0a0a0a 0%,
        rgba(10, 10, 10, 0) 100%
      ),
      radial-gradient(
        75% 75% at 50% 50%,
        rgba(28, 28, 28, 0) 63.74%,
        #1c1c1c 100%
      ),
      url(../img/bg-popup.jpg);
    @include bg-cover;
    padding-bottom: 74px;

    img {
      padding-top: 56px;
      padding-bottom: 19px;
    }
  }

  &__title {
    font-weight: 800;
    font-size: 46px;
    line-height: 63px;
    text-align: center;
    letter-spacing: 0.02em;
    text-transform: uppercase;
    color: var(--light-color);
    margin: 0;
    margin-bottom: 6px;
  }

  &__text {
    font-weight: 400;
    font-size: 20px;
    line-height: 23px;
    text-align: center;
    color: var(--light-color);
    margin: 0;
  }

  &__bottom {
    padding: 70px 15px;
  }

  &__subtitle {
    font-weight: 500;
    font-size: 24px;
    line-height: 28px;
    text-align: center;
    color: var(--text-color);
    margin: 0;
    padding-bottom: 38px;
  }

  &__btns {
    padding-bottom: 65px;
  }

  &__btn {
    border: none;

    &:first-child {
      margin-right: 16px;
      color: var(--accent-color);
      background-color: var(--light-color);
      border: 1px solid var(--accent-color);
    }
  }

  &__info-text {
    font-weight: 400;
    font-size: 14px;
    line-height: 160%;
    color: #808080;

    a {
      text-decoration-line: underline;
      color: var(--text-color);
    }
  }
}
