.menu-right__text {
  @include flex-v-center;
  padding-bottom: 26px;
  padding-right: 25px;

  @include for-desktop {
    padding-right: 0;
  }

  .text {
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    color: var(--light-color);
    margin: 0 11px;

    @include for-desktop {
      margin-right: 0;
    }
  }
}
