.benefits {
  max-width: 1440px;
  margin: 0 auto;

  .swiper {
    position: relative;

    &__slide {
      display: flex;
    }

    .benefits__item {
      display: flex;

      @include for-desktop {
        flex-direction: column-reverse;
        align-items: center;
      }

      picture {
        @include for-desktop {
          padding-bottom: 121px;
        }

        img {
          @include for-desktop {
            height: auto;
            max-width: 100%;
            object-fit: cover;
          }
        }
      }
    }
  }

  &__nav {
    position: absolute;
    bottom: 60px;
    right: 34%;
    z-index: 2;
  }

  &__info {
    padding-left: 80px;
    max-width: 475px;

    @include for-desktop {
      padding-left: 0;
      text-align: center;
    }
  }

  &__subtitle {
    margin: 0;
    padding-top: 75px;
    padding-bottom: 16px;
    font-weight: 500;
    font-size: 30px;
    line-height: 41px;
    color: var(--accent-color);

    @include for-desktop {
      padding-top: 32px;
      padding-bottom: 8px;
      font-size: 20px;
      line-height: 28px;
    }
  }

  &__text {
    font-weight: 400;
    font-size: 16px;
    line-height: 160%;
    color: var(--dark-color);
    margin-bottom: 48px;
  }

  &__link {
    padding-top: 48px;

    @include for-desktop {
      padding-top: 32px;
      padding-bottom: 30px;
    }
  }

  &__btn {
    transition: 0.3s ease-in-out;

    &:hover {
      background: var(--accent-hover-color);
    }
  }

  &__btns {
    display: flex;
    align-items: center;

    .swiper-pagination {
      display: contents;
      align-items: center;

      .swiper-pagination-bullet {
        width: 6px;
        height: 6px;
      }

      .swiper-pagination-bullet-active {
        width: 8px;
        height: 8px;
        background: var(--accent-color);
      }
    }

    .benefits__slider-btn--prev {
      margin-right: 22px;
    }

    .benefits__slider-btn--next {
      margin-left: 22px;
    }

    .benefits__slider-btn {
      svg {
        width: 32px;
        height: 32px;
        border-radius: 100%;
        border: 1px solid var(--accent-color);
        transition: 0.3s;

        &:hover {
          background-color: var(--accent-color);
        }
      }
    }
  }
}
