.title {
  margin: 0;
  margin-bottom: 24px;
  font-weight: 800;
  font-size: 46px;
  line-height: 63px;
  letter-spacing: 0.02em;
  text-transform: uppercase;
  color: var(--text-color);

  @include tablet {
    display: flex;
    align-items: center;
    flex-direction: column;
    margin-bottom: 38px;
    font-size: 26px;
    line-height: 35px;
  }
}
