.about {
  &__container {
    margin-top: 90px;
    margin-bottom: 94px;

    @include mobile {
      margin-bottom: 40px;
    }
  }

  &__descr-top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-bottom: 48px;

    @include tablet {
      flex-direction: column;
    }
  }

  &__title {
    position: relative;
  }

  &__title::before {
    content: "";
    position: absolute;
    top: 32px;
    left: 175px;
    width: 50px;
    height: 2px;
    background: var(--accent-color);

    @include tablet {
      top: 50px;
      left: 326px;
    }

    @include mobile {
      left: 150px;
    }
  }

  .descr-top__text {
    margin: 0;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 160%;
    color: var(--text-color);
    max-width: 564px;

    @include tablet {
      padding-bottom: 26px;
      text-align: center;
      max-width: 700px;
    }
  }
  .descr-top__img {
    max-width: 100%;
  }

  &__descr-bottom {
    display: flex;
    align-items: center;
    padding-bottom: 80px;

    @include tablet {
      flex-direction: column-reverse;
    }

    @include mobile {
      padding-bottom: 32px;
    }
  }

  .descr-bottom__img {
    max-width: 100%;
  }

  .descr-bottom__text {
    margin: 0;
    margin-left: 66px;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 160%;
    color: var(--text-color);
    max-width: 540px;

    @include tablet {
      margin: 0;
      padding-bottom: 26px;
      text-align: center;
      max-width: 700px;
    }
  }

  &__link {
    text-align: center;
  }

  &__btn-link {
    border: 1px solid #000000;
    transition: 0.3s ease-in-out;

    &:hover {
      background: var(--accent-hover-color);
    }
  }
}
